<template>
  <el-container direction="vertical">
    <el-header height="">
      <!-- Header content -->
      <el-row>
        <el-button type="primary" style="float:right" @click="createApp">创建</el-button>
      </el-row>
    </el-header>
    <el-container direction="horizontal">
      <!-- <el-container direction="vertical"> -->
        <el-main height="">
          <el-table :data="applicationList" style="width: 100%">
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="id">
                    <span>{{ props.row.id }}</span>
                  </el-form-item>
                  <el-form-item label="名称">
                    <span>{{ props.row.name }}</span>
                  </el-form-item>
                  <el-form-item label="镜像">
                    <span>{{ props.row.image }}</span>
                  </el-form-item>
                  <el-form-item label="启动命令">
                    <span>{{ props.row.cmd }}</span>
                  </el-form-item>
                  <el-form-item label="目标实例数">
                    <span>{{ props.row.target }}</span>
                  </el-form-item>
                  <el-form-item label="实际实例数">
                    <span>{{ props.row.ready }}</span>
                  </el-form-item>
                  <el-form-item label="已更新实例数">
                    <span>{{ props.row.update }}</span>
                  </el-form-item>
                  <el-form-item label="可用实例数">
                    <span>{{ props.row.available }}</span>
                  </el-form-item>
                  <el-form-item label="创建时间">
                    <span>{{ props.row.create_time }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="名称" prop="name" width="140px"> </el-table-column>
            <el-table-column label="镜像" prop="image" width="140px"> </el-table-column>
            <el-table-column label="目标实例数" prop="target" width="100px"> </el-table-column>
            <el-table-column label="实际实例数" prop="ready" width="100px"> </el-table-column>
            <el-table-column label="已更新实例数" prop="update" width="140px"> </el-table-column>
            <el-table-column label="可用实例数" prop="available" width="100px"> </el-table-column>
            <el-table-column label="创建时间" prop="create_time"></el-table-column>
            <el-table-column align="right">
              <template slot="header">
                <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
              </template>
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)" >Edit</el-button >
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)" >Delete</el-button >
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      <!-- </el-container> -->
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      applications: {},
      applicationList: [],
      search: "",
      refresh_app_timer: null
    };
  },

  mounted() {
    console.log("Apps mounted")
    this.listApp()
    this.refresh_app_timer = setInterval(this.listApp, 5000)
  },
  destroyed() {
    clearInterval(this.refresh_app_timer)
  },
  methods: {
    handleEdit(app) {
      // console.log(app)
      this.$router.push("/viewAppsCreate?appId="+app.id)
    },
    handleDelete(app) {
      this.deleteRequest("/application?appId="+app.id)
      this.listApp()
    },
    createApp() {
      this.$router.push("/viewAppsCreate")
    },
    listApp() {
      this.applications = {}
      this.applicationList = []
      let _this = this
      this.getRequest("/application").then(function(applicationList){
        applicationList.forEach(application => {
          let app = {
            "id": application.id,
            "name": application.name,
            "command": application.command,
            "image": application.image,
            "target": application.target,
            "pullPolicy": "",
            "ready": 0,
            "available": 0,
            "update": 0,
            "create_time": application.createTime
          }
          _this.applications[application.id] = app
          _this.applicationList.push(app)
        });
      })
    }
  },
};
</script>

<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>